body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


button{
  cursor: pointer;
}

body{
  background-color: #f6f7fa;
  background-size: cover;
}

.app-container{
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: 60px auto;
  max-width: 1366px;
  margin: auto;
}

@media (max-width: 700px) {
  .app-container{
      display: flex; 
      flex-direction: column; 
      width: 100%;
            height: -webkit-fit-content;
            height: -moz-fit-content;
            height: fit-content;
    }
}




  /* font-size: calc(10px + 2vmin); */


.header-container{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    border-bottom: 1px solid lightgray;
    height: 65px;
    background-color: #ffffff;
    grid-column: 2/3;
    max-width: 100%;
}

.header-container li{
  list-style: none;
}

.left-header, .right-header {
 display: flex;
 flex-direction: row;
 align-items: center;
}

.left-header li{
    padding: 0 10px;
}

.right-header  li{
  padding: 0 10px;
}
.side-manu-btn{
   padding: 5px;
   display: flex; 
}


.left-arrow{
   width: 6px;
   height: 12px;
   align-self: center;
   padding: 5px;
}

.transition{
    transition: all 2s ease;
}

.left-header li:nth-child(1) img:nth-child(2) {
    height:18px;
}

.left-header li:nth-child(2) img,
.right-header li:nth-child(2) img {
    height: 20px;
}

.right-header li:nth-child(3) img {
    height: 22px;
}


.add-project-btn{
   
    background-color: #5584ff;
    height: 40px;
    border-radius: 25px;
    color: #ffffff;
    display: flex;
    margin: 0 10px;

}

.add-project-btn h6{
    margin: 0 auto;
    align-self: center;
    font-size: 15px;
    font-weight: 600;
    letter-spacing: 0.24px;
    padding: 2px 8px 2px 0;
    text-align: left;
}

.add-project-btn img{
    width: 10px;
    height: 10px;
    align-self: center;
    padding: 8px;
}

.user-area{
    display: flex;
    align-items: center;
}

.user-img{
    margin-right: 5px;
    height: 35px;
    width: 35px;
}

.user-area img:nth-child(2){
    height: 5px;
}



@media (max-width: 860px) {
    
  
    .left-header, .right-header{
        padding: 0;
      }
}

@media(max-width:700px){
    .header-container{
        max-width: 100%;
      }
}
@media (max-width: 500px) {
    .left-arrow{
       display: none;
      }
     .user-area img:nth-child(2){
         display: none;
      }

     .header-container li {
        margin: 2px;
      }
    }



.nav-container{
   max-width: 240px;
   height: 1600px;
   background-color: #0e1a35;
   grid-row: 1/3;
   display: flex;
   flex-direction: column;
}

.logo{
    height: 65px;
    border-bottom: 2px solid rgba(11, 10, 16,0.2);
}
 .logo img{
    max-width: 160px;
    height: 42px;
    align-self: center;
    padding: 10px 40px;
}

.left-navbar{
    list-style: none;
    padding: 0;
    margin: 0;
}

.left-navbar a  img{
    max-height: 20px;
    width: 20px;
    padding-right: 15px;
}
.left-navbar a {
    border-bottom: 2px solid rgba(11, 10, 16,0.2);
    height: 48px;
    display: flex;
    align-items: center;
    padding: 1.8px 0;
    transition: all 0.3s;
}

.left-navbar a:hover, .selected{
    border-left: 4px solid #5584ff;
    background-color: #122143;
}



.left-navbar  a  {
    text-decoration: none;
    color: #ffffff;
    font-size: 18px;
     font-weight: 500;
     line-height: 48px;
} 

.left-navbar  a li{
   margin-left: 20%;
}

@media(max-width:940px){
    .nav-container{
        height: 1900px;
    }
}

@media (max-width: 700px) {
    .nav-container{
        max-width: 100%;
        display: flex;
        flex-direction: row;
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;
    }

    .left-navbar{
        display: flex;
        flex-direction: row;
        background-color: #0e1a35;
        margin: auto;
    }

    .left-navbar a:hover, .selected{
        border-left: none;
        border-bottom: 4px solid #5584ff;
        background-color: #122143;
    }

    .left-navbar a li {
        display: flex;
        flex-direction: row;
        align-items: center;
        padding: 0 10.5px;
        margin-left: 0;
    }

    .logo {
        display: none;
    }

    .left-navbar a img{
        display: none;
    }

    .left-navbar a {
        font-size: 3vw;
        border-right: 2px solid rgba(11, 10, 16,0.2);
  }
}


